// ==============================================================================
//           Service pour les Appels API lis  l'Authentification
//
// Ce service encapsule tous les appels rseau (via Axios) vers les
// endpoints d'authentification de l'API.
// Il est utilis par les thunks/actions Redux pour interagir avec le backend.
// ==============================================================================

import apiClient from './api';
import { API_ENDPOINTS } from '../utils/constants';

/**
 * Envoie une requte de connexion  l'API.
 * @param {{email: string, password: string}} credentials - Les identifiants de l'utilisateur.
 * @returns {Promise<object>} Les donnes de la rponse (contenant user, accessToken, etc.).
 */
const login = async (credentials) => {
  const response = await apiClient.post(API_ENDPOINTS.LOGIN, credentials);
  return response.data;
};

/**
 * Envoie une requte d'inscription  l'API.
 * @param {object} userData - Les donnes du nouvel utilisateur.
 * @returns {Promise<object>} Les donnes de la rponse.
 */
const register = async (userData) => {
  const response = await apiClient.post(API_ENDPOINTS.REGISTER, userData);
  return response.data;
};

/**
 * Envoie une requte de dconnexion  l'API.
 * Le backend peut alors invalider le refresh token si ncessaire.
 * @returns {Promise<object>}
 */
const logout = async () => {
  // Mme si la logique principale est de supprimer les tokens ct client,
  // appeler un endpoint de dconnexion est une bonne pratique pour permettre
  // au backend de faire du nettoyage (ex: invalider un refresh token).
  const response = await apiClient.post(API_ENDPOINTS.LOGOUT, {});
  return response.data;
};

/**
 * Envoie une requte pour demander un lien de rinitialisation de mot de passe.
 * @param {string} email - L'email de l'utilisateur.
 * @returns {Promise<object>}
 */
const requestPasswordReset = async (email) => {
  const response = await apiClient.post(API_ENDPOINTS.FORGOT_PASSWORD, { email });
  return response.data;
};

/**
 * Envoie le token et le nouveau mot de passe  l'API pour finaliser la rinitialisation.
 * @param {string} token - Le token de rinitialisation reu de l'URL.
 * @param {string} password - Le nouveau mot de passe.
 * @returns {Promise<object>}
 */
const resetPassword = async (token, password) => {
  // Construit l'URL dynamique avec le token
  const url = `${API_ENDPOINTS.RESET_PASSWORD}/${token}`;
  const response = await apiClient.patch(url, { password });
  return response.data;
};


// On exporte un objet contenant toutes les fonctions du service
const authService = {
  login,
  register,
  logout,
  requestPasswordReset,
  resetPassword,
};

export default authService;