// ==============================================================================
//           Service pour les Appels API du Tableau de Bord
//
// Rle : Ce fichier centralise tous les appels API lis  la rcupration
// des donnes pour les diffrents tableaux de bord de l'application.
//
// Il favorise un endpoint unique (`getMainDashboardData`) pour optimiser
// le chargement initial.
// ==============================================================================

import apiClient from './api';
import { API_ENDPOINTS } from '../utils/constants';

/**
 * [MTHODE PRINCIPALE] Rcupre toutes les donnes agrges pour le tableau de
 * bord principal en un seul appel API. C'est la mthode  utiliser pour le
 * chargement initial de la page.
 * @returns {Promise<object>} Un objet contenant toutes les donnes du dashboard
 *                            (kpis, salesChart, etc.).
 */
const getMainDashboardData = async () => {
  // Cet endpoint unique est gr par `dashboardController.js` ct backend.
  // Note: API_ENDPOINTS.DASHBOARD est juste '/dashboard'
  const response = await apiClient.get(API_ENDPOINTS.DASHBOARD);
  return response.data;
};


// --- Fonctions Individuelles (pour des rafrachissements cibls) ---
// Ces fonctions ne sont utiles que si une fonctionnalit ncessite de
// rafrachir une seule partie du tableau de bord sans recharger le reste.

/**
 * Rcupre uniquement les indicateurs de performance cls (KPIs) commerciaux.
 * @returns {Promise<object>} L'objet contenant les KPIs.
 */
const getKpisCommerciaux = async () => {
  const response = await apiClient.get(`${API_ENDPOINTS.DASHBOARD}/kpis/commerciaux`);
  return response.data;
};

/**
 * Rcupre uniquement les donnes pour le graphique des ventes annuelles.
 * @returns {Promise<object>}
 */
const getSalesChartData = async () => {
  const response = await apiClient.get(`${API_ENDPOINTS.DASHBOARD}/charts/sales`); // Exemple de route
  return response.data;
};


// On exporte un objet contenant toutes les fonctions du service.
const dashboardService = {
  getMainDashboardData,
  getKpisCommerciaux,
  getSalesChartData,
};

export default dashboardService;