// ==============================================================================
//           Hook Personnalis pour la Vrification des Permissions
//
// Ce hook est une surcouche simple au hook `useAuth`. Son seul but est de
// fournir une API plus claire et plus smantique pour la vrification des
// permissions dans les composants.
//
// Il abstrait la logique de " fouiller " dans l'objet `user` pour trouver les
// permissions, rendant les composants plus lisibles.
// ==============================================================================

import { useAuth } from './useAuth';
import { useCallback } from 'react';

/**
 * Hook pour vrifier facilement les permissions de l'utilisateur connect.
 * @returns {{
 *  permissions: Set<string>,
 *  hasPermission: (permission: string) => boolean,
 *  hasAnyPermission: (permissions: string[]) => boolean
 * }}
 */
export const usePermissions = () => {
  const { permissions } = useAuth(); // Rcupre le Set de permissions depuis useAuth

  /**
   * Vrifie si l'utilisateur possde une permission spcifique.
   */
  const hasPermission = useCallback(
    (requiredPermission) => {
      if (!requiredPermission) return true; // Si aucune permission n'est requise, on autorise
      return permissions.has(requiredPermission);
    },
    [permissions]
  );
  
  /**
   * Vrifie si l'utilisateur possde au moins une des permissions listes.
   */
  const hasAnyPermission = useCallback(
    (requiredPermissions = []) => {
      if (requiredPermissions.length === 0) return true;
      return requiredPermissions.some(p => permissions.has(p));
    },
    [permissions]
  );


  return { permissions, hasPermission, hasAnyPermission };
};