// ==============================================================================
//                Composant de Layout Principal de l'Application
//
// Rle : Assemble les composants structurels majeurs (Sidebar, Header, Footer)
// et le contenu des routes (via <Outlet>).
//
// Gre l'tat de la sidebar (ouverte/ferme) via Redux pour permettre
// une interaction entre le Header (qui dclenche) et la Sidebar (qui ragit).
// ==============================================================================

import React, { useEffect } from 'react';
import { Outlet, useLocation } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';

// --- Importations des composants de structure ---
import Sidebar from './Sidebar';
import Header from './Header';
import Footer from './Footer';

// --- Importation des actions Redux pour grer l'UI ---
import { toggleSidebar, closeSidebar } from '../../store/slices/uiSlice';

const Layout = () => {
  const dispatch = useDispatch();
  const location = useLocation();

  // On lit l'tat de la sidebar directement depuis le store Redux.
  const isSidebarOpen = useSelector((state) => state.ui.isSidebarOpen);

  // --- Handlers pour interagir avec l'tat de l'UI ---
  const handleToggleSidebar = () => {
    dispatch(toggleSidebar());
  };
  
  const handleCloseSidebar = () => {
    dispatch(closeSidebar());
  };
  
  // Effet pour fermer automatiquement la sidebar lors d'un changement de page sur mobile.
  useEffect(() => {
    if (isSidebarOpen) {
      handleCloseSidebar();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [location]);


  return (
    <div className="layout-wrapper">
      {/* 
        Le menu latral. Sa classe CSS change en fonction de l'tat Redux,
        ce qui dclenche les animations/transitions dfinies dans `responsive.css`.
      */}
      <Sidebar className={isSidebarOpen ? 'is-open' : ''} />

      {/*
        L'overlay s'affiche uniquement en mode mobile lorsque le menu
        est ouvert. Un clic dessus ferme le menu.
      */}
      <div
        className={`sidebar-overlay ${isSidebarOpen ? 'is-active' : ''}`}
        onClick={handleCloseSidebar}
        aria-hidden="true"
      />

      <div className="content-wrapper">
        {/* 
          On passe la fonction `handleToggleSidebar` au Header, qui contiendra
          le bouton "hamburger" pour ouvrir le menu sur les petits crans.
        */}
        <Header onToggleSidebar={handleToggleSidebar} />
        
        <main className="main-content">
          {/* Outlet est l'endroit o React Router affichera le composant de la page active */}
          <Outlet />
        </main>
        
        <Footer />
      </div>
    </div>
  );
};

export default Layout;