// ==============================================================================
//           Contexte, Provider et Hooks pour la Gestion de Socket.IO
//
// Ce module fournit une architecture complte pour grer une connexion
// Socket.IO unique et persistante  travers l'application React.
//
// - SocketContext : Le contexte React lui-mme.
// - SocketProvider : Le composant qui gre le cycle de vie de la connexion.
// - useSocket : Le hook pour accder  l'instance du socket et  son tat.
// - useSocketEvent : Hook d'aide pour s'abonner aux vnements de manire robuste.
// ==============================================================================

import React, {
  useState,
  useEffect,
  useMemo,
  useCallback,
  useContext,
  createContext,
  useRef
} from 'react';
import { io } from 'socket.io-client';
import { useSelector } from 'react-redux';

// L'URL de votre serveur backend
const SOCKET_URL = import.meta.env.VITE_SOCKET_URL || 'http://localhost:5000';

// 1. Cration du Contexte React
const SocketContext = createContext(null);

/**
 * Le Provider qui va grer la connexion Socket.IO.
 * @param {{children: React.ReactNode}} props
 */
export const SocketProvider = ({ children }) => {
  const [socket, setSocket] = useState(null);
  const [isConnected, setIsConnected] = useState(false);

  // Rcupration du token via Redux
  const token = useSelector((state) => state.auth.token);

  useEffect(() => {
    if (token) {
      const newSocket = io(SOCKET_URL, {
        auth: { token },
        reconnection: true,
        reconnectionAttempts: 5,
      });

      setSocket(newSocket);

      newSocket.on('connect', () => setIsConnected(true));
      newSocket.on('disconnect', () => setIsConnected(false));
      newSocket.on('connect_error', (err) => console.error('Erreur de connexion WebSocket:', err.message));

      return () => {
        newSocket.disconnect();
      };
    } else if (socket) {
      socket.disconnect();
      setSocket(null);
      setIsConnected(false);
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [token]);

  const contextValue = useMemo(() => ({ socket, isConnected }), [socket, isConnected]);

  return (
    <SocketContext.Provider value={contextValue}>
      {children}
    </SocketContext.Provider>
  );
};

/**
 * Hook personnalis pour accder  l'instance du socket et  son tat de connexion.
 * @returns {{socket: import('socket.io-client').Socket | null, isConnected: boolean}}
 */
export const useSocket = () => {
  const context = useContext(SocketContext);
  if (!context) {
    throw new Error("useSocket doit tre utilis  l'intrieur d'un <SocketProvider>");
  }
  return context;
};

/**
 * Hook d'aide pour s'abonner  un vnement Socket.IO en toute scurit.
 * Gre automatiquement l'abonnement et le dsabonnement.
 * @param {string} eventName
 * @param {(...args: any[]) => void} handler - Le callback  excuter
 */
export const useSocketEvent = (eventName, handler) => {
  const { socket } = useSocket();
  
  // On utilise un ref pour stocker la dernire version du handler,
  // ce qui vite de devoir recrer l'abonnement  chaque re-render du composant parent.
  const handlerRef = useRef(handler);
  
  useEffect(() => {
    handlerRef.current = handler;
  }, [handler]);

  useEffect(() => {
    if (!socket) return;

    // Le wrapper s'assure d'appeler la version la plus "frache" du handler.
    const wrapper = (...args) => {
      if (handlerRef.current) {
        handlerRef.current(...args);
      }
    };

    socket.on(eventName, wrapper);
    
    return () => {
      socket.off(eventName, wrapper);
    };
  }, [socket, eventName]);
};