// ==============================================================================
//                Composant Indicateur de Chargement (Loader)
//
// Ce composant rutilisable affiche un indicateur de chargement ("spinner").
// Il est conu pour tre flexible et peut tre affich de plusieurs manires :
//   - En tant que petit spinner "inline" pour les boutons.
//   - En tant que spinner centr pour le chargement d'une page ou d'un composant.
//   - En tant que spinner en plein cran avec un overlay.
//
// Il utilise le composant `Spinner` de `react-bootstrap`.
// ==============================================================================

import React from 'react';
import { Spinner } from 'react-bootstrap';

/**
 * Affiche un indicateur de chargement (spinner).
 *
 * @param {{
 *  fullscreen?: boolean,
 *  centered?: boolean,
 *  variant?: string,
 *  size?: 'sm' | 'md',
 *  text?: string,
 *  showText?: boolean,
 * }} props
 */
const Loader = ({
  fullscreen = false,
  centered = false,
  variant = 'primary',
  size = 'md',
  text = 'Chargement...',
  showText = false,
}) => {

  const spinnerComponent = (
    <div className="d-flex align-items-center">
      <Spinner
        animation="border"
        variant={variant}
        role="status"
        size={size === 'sm' ? 'sm' : undefined}
        // Pour les tailles md et plus, on peut utiliser des classes CSS pour la taille si besoin
        style={size !== 'sm' ? { width: '2.5rem', height: '2.5rem' } : {}}
      >
        <span className="visually-hidden">{text}</span>
      </Spinner>
      {showText && <span className="ms-2">{text}</span>}
    </div>
  );

  // --- Variante 1: Loader en plein cran avec overlay ---
  // Idal pour le chargement initial de l'application ou une action bloquante majeure.
  if (fullscreen) {
    return (
      <div
        className="d-flex justify-content-center align-items-center"
        style={{
          position: 'fixed',
          top: 0,
          left: 0,
          width: '100%',
          height: '100%',
          // Utilise les variables du thme pour le fond de l'overlay
          backgroundColor: 'rgba(var(--theme-bg-primary-rgb, 255, 255, 255), 0.7)',
          zIndex: 'var(--z-index-fullscreen-loader, 9999)',
        }}
      >
        {spinnerComponent}
      </div>
    );
  }

  // --- Variante 2: Loader centr dans son conteneur ---
  // Utile pour le chargement d'une carte (Card) ou d'un tableau.
  if (centered) {
    return (
      <div className="d-flex justify-content-center align-items-center w-100 h-100 my-5">
        {spinnerComponent}
      </div>
    );
  }

  // --- Variante 3: Loader "inline" (par dfaut) ---
  // Parfait pour tre plac  l'intrieur d'un bouton.
  return spinnerComponent;
};

export default Loader;