// ==============================================================================
//           Dfinition Centrale des Routes de l'Application
//
// Ce fichier utilise `react-router-dom` pour dfinir la structure de navigation
// de l'application. Il organise les routes en deux catgories principales :
//   - Publiques : Accessibles  tous (ex: connexion, inscription).
//   - Prives : Ncessitent une authentification.
//
// Il utilise des composants "wrapper" (PublicRoute, PrivateRoute) pour grer
// la logique de redirection en fonction de l'tat d'authentification.
// ==============================================================================

import React from 'react';
import { Routes, Route } from 'react-router-dom';

// --- Importation des Gardiens et du Composant Racine ---
import PrivateRoute from './PrivateRoute';
import PublicRoute from './PublicRoute';
import Root from './Root'; // Le composant d'aiguillage initial

// --- Importation des Layouts et Pages ---
import Layout from '../components/common/Layout';

// Pages Publiques (Authentification)
import LoginPage from '../pages/auth/Login';
import RegisterPage from '../pages/auth/Register';
import ForgotPasswordPage from '../pages/auth/ForgotPassword';
//import ResetPasswordPage from '../pages/auth/ResetPassword';

// Pages Protges
import DashboardPage from '../pages/dashboard/Dashboard';
// import ClientsListPage from '../pages/clients/ClientsList';
// import ClientFormPage from '../pages/clients/ClientForm';
// import ClientDetailPage from '../pages/clients/ClientDetail';

// Page d'Erreur
import NotFoundPage from '../pages/errors/NotFound';

// Composant "placeholder" pour les pages non encore cres
const Placeholder = ({ title }) => <h2 className="text-muted mt-4">{title} -  construire</h2>;


const AppRoutes = () => {
  return (
    <Routes>
      {/* ================================================== */}
      {/* ===           ROUTE RACINE                     === */}
      {/* ================================================== */}
      {/* Cette route est la premire  tre value. Le composant `Root`
          redirige l'utilisateur vers /login ou /dashboard en fonction de
          son tat de connexion. */}
      <Route path="/" element={<Root />} />


      {/* ================================================== */}
      {/* ===           ROUTES PUBLIQUES                 === */}
      {/* ================================================== */}
      {/* Ces routes sont enveloppes par `PublicRoute`. Si l'utilisateur est
          dj connect, il sera redirig vers `/dashboard`. */}
      <Route element={<PublicRoute redirectTo="/dashboard" />}>
        <Route path="/login" element={<LoginPage />} />
        <Route path="/register" element={<RegisterPage />} />
        <Route path="/forgot-password" element={<ForgotPasswordPage />} />
       {/* <Route path="/reset-password/:token" element={<ResetPasswordPage />} /> */}
      </Route>


      {/* ================================================== */}
      {/* ===           ROUTES PROTGES                 === */}
      {/* ================================================== */}
      {/* Ces routes sont enveloppes par `PrivateRoute`. Si l'utilisateur n'est
          pas connect, il sera redirig vers `/login`. */}
      <Route element={<PrivateRoute redirectTo="/login" />}>
        {/* Toutes les routes  l'intrieur de cette section utiliseront le `Layout`
            (qui contient la sidebar, le header, etc.). */}
        <Route element={<Layout />}>
          <Route path="/dashboard" element={<DashboardPage />} />
          
          {/* --- Module Clients --- */}
          {/* <Route path="/clients" element={<ClientsListPage />} /> */}
          {/* <Route path="/clients/nouveau" element={<ClientFormPage key="client-create" />} /> */}
          {/* <Route path="/clients/:id/modifier" element={<ClientFormPage key="client-edit" />} /> */}
          {/* <Route path="/clients/:id" element={<ClientDetailPage />} /> */}
          
          {/* --- Placeholders pour les futurs modules --- */}
          <Route path="/fournisseurs" element={<Placeholder title="Fournisseurs" />} />
          <Route path="/produits" element={<Placeholder title="Produits & Services" />} />
          <Route path="/ventes" element={<Placeholder title="Ventes" />} />
          <Route path="/stock" element={<Placeholder title="Stock" />} />
          <Route path="/comptabilite" element={<Placeholder title="Comptabilit" />} />
          <Route path="/parametres" element={<Placeholder title="Paramtres" />} />
        </Route>
      </Route>


      {/* ================================================== */}
      {/* ===           ROUTE 404                        === */}
      {/* ================================================== */}
      {/* Cette route est value en dernier. Si aucune des routes
          ci-dessus n'a match, elle s'affichera. */}
      <Route path="*" element={<NotFoundPage />} />
    </Routes>
  );
};

export default AppRoutes;