// ==============================================================================
//           Service pour les Appels API lis aux Clients (Version Finale)
//
// Rle : Ce service est la seule porte d'entre pour toutes les interactions
// avec la ressource "Client" de l'API. Il abstrait la logique des requtes HTTP.
//
// Bonnes Pratiques :
// - Utilisation d'un `apiClient` centralis (instance Axios).
// - Centralisation des endpoints via `API_ENDPOINTS`.
// - Gestion d'erreurs explicite avec `try...catch` dans chaque fonction.
// - Documentation JSDoc dtaille pour chaque mthode.
// ==============================================================================

import apiClient from './api';
import { API_ENDPOINTS } from '../utils/constants';

/**
 * Rcupre une liste pagine et filtre de clients.
 * @param {object} [params] - Les paramtres de la requte (page, limit, sort, filtres).
 * @throws {Error} Relance l'erreur API si l'appel choue.
 * @returns {Promise<object>} La rponse de l'API (gnralement { data: [...], pagination: {...} }).
 */
const getAll = async (params) => {
  try {
    const response = await apiClient.get(API_ENDPOINTS.CLIENTS, { params });
    return response.data;
  } catch (error) {
    console.error("Erreur lors de la rcupration de la liste des clients :", error.response?.data || error.message);
    throw error;
  }
};

/**
 * Rcupre un client spcifique par son ID.
 * @param {string} id - L'ID unique du client.
 * @throws {Error} Relance l'erreur API si l'appel choue.
 * @returns {Promise<object>} La rponse de l'API contenant les donnes du client.
 */
const getById = async (id) => {
  try {
    const response = await apiClient.get(`${API_ENDPOINTS.CLIENTS}/${id}`);
    return response.data;
  } catch (error) {
    console.error(`Erreur lors de la rcupration du client ${id} :`, error.response?.data || error.message);
    throw error;
  }
};

/**
 * Cre un nouveau client.
 * @param {object} clientData - Les donnes du client  crer.
 * @throws {Error} Relance l'erreur API si l'appel choue.
 * @returns {Promise<object>} La rponse de l'API contenant le client nouvellement cr.
 */
const create = async (clientData) => {
  try {
    const response = await apiClient.post(API_ENDPOINTS.CLIENTS, clientData);
    return response.data;
  } catch (error) {
    console.error("Erreur lors de la cration du client :", error.response?.data || error.message);
    throw error;
  }
};

/**
 * Met  jour un client existant.
 * @param {string} id - L'ID du client  mettre  jour.
 * @param {object} updateData - Les champs  mettre  jour.
 * @throws {Error} Relance l'erreur API si l'appel choue.
 * @returns {Promise<object>} La rponse de l'API contenant le client mis  jour.
 */
const update = async (id, updateData) => {
  try {
    const response = await apiClient.patch(`${API_ENDPOINTS.CLIENTS}/${id}`, updateData);
    return response.data;
  } catch (error) {
    console.error(`Erreur lors de la mise  jour du client ${id} :`, error.response?.data || error.message);
    throw error;
  }
};

/**
 * Supprime (ou dsactive) un client.
 * @param {string} id - L'ID du client  supprimer.
 * @throws {Error} Relance l'erreur API si l'appel choue.
 * @returns {Promise<void>}
 */
const remove = async (id) => {
  try {
    // La rponse pour un DELETE russi est souvent un statut 204 No Content, sans corps.
    await apiClient.delete(`${API_ENDPOINTS.CLIENTS}/${id}`);
  } catch (error) {
    console.error(`Erreur lors de la suppression du client ${id} :`, error.response?.data || error.message);
    throw error;
  }
};

/**
 * [EXEMPLE D'EXTENSION] Rcupre les statistiques d'un client spcifique.
 * @param {string} id - L'ID du client.
 * @throws {Error} Relance l'erreur API si l'appel choue.
 * @returns {Promise<object>} Un objet contenant les statistiques (ex: CA total, factures impayes).
 */
const getClientStats = async (id) => {
  try {
    const response = await apiClient.get(`${API_ENDPOINTS.CLIENTS}/${id}/stats`);
    return response.data;
  } catch (error) {
    console.error(`Erreur lors de la rcupration des stats du client ${id} :`, error.response?.data || error.message);
    throw error;
  }
};


// On exporte un objet contenant toutes les fonctions du service.
const clientsService = {
  getAll,
  getById,
  create,
  update,
  remove,
  getClientStats, // On ajoute la nouvelle fonction  l'export
};

export default clientsService;