// ==============================================================================
//           Composant Graphique en Barres (BarChart)
//
// Ce composant est un "wrapper" autour de `react-chartjs-2` pour afficher
// des graphiques en barres de manire cohrente.
//
// Comme le LineChart, il est pilot par les props `data` et `options` et
// configure des options par dfaut pour un design propre.
// ==============================================================================

import React from 'react';
import { Bar } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement, // lment spcifique pour les graphiques en barres
  Title,
  Tooltip,
  Legend,
} from 'chart.js';

// --- Enregistrement des composants Chart.js ---
ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement, // On enregistre BarElement
  Title,
  Tooltip,
  Legend
);

/**
 * Affiche un graphique en barres configurable.
 *
 * @param {{
 *  data: import('chart.js').ChartData<'bar'>,
 *  options?: import('chart.js').ChartOptions<'bar'>
 * }} props
 */
const BarChart = ({ data, options }) => {
  // Options par dfaut pour un graphique en barres
  const defaultOptions = {
    responsive: true,
    maintainAspectRatio: false, // Important pour contrler la hauteur via CSS
    plugins: {
      legend: {
        display: true,
        position: 'top',
        labels: {
          color: '#6c757d',
        },
      },
      title: {
        display: false, // Le titre est gr par le composant parent (ex: Card.Header)
      },
      tooltip: {
        mode: 'index',
        intersect: false,
        backgroundColor: '#343a40',
      },
    },
    scales: {
        x: {
            grid: {
                display: false, // Masquer la grille verticale
            },
            ticks: {
                color: '#6c757d',
            },
        },
        y: {
            beginAtZero: true, // L'axe Y commence toujours  0 pour les barres
            grid: {
                color: '#e9ecef',
            },
            ticks: {
                color: '#6c757d',
            },
        },
    },
    indexAxis: 'x', // 'x' pour des barres verticales, 'y' pour des barres horizontales
  };

  // Fusionne les options par dfaut avec celles passes en props.
  const chartOptions = { ...defaultOptions, ...options };

  return <Bar options={chartOptions} data={data} />;
};

export default BarChart;