// ==============================================================================
//                  Utilitaire de Manipulation des Devises (Client)
//
// Ce fichier centralise toutes les fonctions lies  la manipulation et au
// formatage des montants montaires.
//
// Il garantit que tous les montants affichs dans l'application sont
// cohrents, lisibles et respectent les conventions de la devise cible
// (principalement le Franc CFA - XOF).
// ==============================================================================

// On dpend de notre numberUtils pour parser les nombres de manire robuste
import { ensureNumber } from './numberUtils';

/**
 * Objet de configuration pour les devises supportes.
 * Peut tre tendu pour grer plusieurs devises.
 */
const CURRENCY_CONFIG = Object.freeze({
  XOF: {
    code: 'XOF',
    symbol: 'FCFA',
    locale: 'fr-SN', // Locale spcifique pour le Sngal
    decimalDigits: 0, // Le Franc CFA n'a gnralement pas de centimes
  },
  EUR: {
    code: 'EUR',
    symbol: '',
    locale: 'fr-FR',
    decimalDigits: 2,
  },
  // ... ajouter d'autres devises si ncessaire
});

/**
 * Formate un montant numrique dans une devise spcifique.
 * C'est la fonction principale de ce module.
 * @param {number | string | null | undefined} amount - Le montant  formater.
 * @param {string} [currencyCode='XOF'] - Le code de la devise (ex: 'XOF', 'EUR').
 * @returns {string} Le montant joliment format.
 *
 * @example
 * formatCurrency(1234567) // "1234567FCFA"
 * formatCurrency(999.75, 'EUR') // "999,75"
 */
export const formatCurrency = (amount, currencyCode = 'XOF') => {
  const config = CURRENCY_CONFIG[currencyCode] || CURRENCY_CONFIG.XOF;
  const number = ensureNumber(amount, 0);

  try {
    return new Intl.NumberFormat(config.locale, {
      style: 'currency',
      currency: config.code,
      minimumFractionDigits: config.decimalDigits,
      maximumFractionDigits: config.decimalDigits,
      currencyDisplay: 'code', // Prfrer 'FCFA'  'F CFA'
    }).format(number);
  } catch (error) {
    console.error(`Erreur lors du formatage de la devise ${currencyCode}:`, error);
    // Fallback simple en cas d'erreur
    return `${number} ${config.symbol}`;
  }
};

/**
 * Convertit un montant format (chane de caractres) en une valeur numrique.
 * Utile pour parser les entres utilisateur dans les formulaires.
 * @param {string} formattedAmount - La chane de caractres  parser (ex: "1.234,56 " ou "1 234 567 FCFA").
 * @returns {number} La valeur numrique.
 *
 * @example
 * parseCurrency("1 234 567 FCFA") // 1234567
 */
export const parseCurrency = (formattedAmount) => {
  if (typeof formattedAmount !== 'string') {
    return ensureNumber(formattedAmount, 0);
  }

  // Expression rgulire pour extraire uniquement les chiffres et le sparateur dcimal (virgule ou point)
  const numericString = formattedAmount
    .replace(/[^0-9,.-]+/g, "") // Supprime tout sauf les chiffres, virgules, points, tirets
    .replace(',', '.'); // Remplace la virgule dcimale par un point
  
  return ensureNumber(parseFloat(numericString), 0);
};

/**
 * Formate un montant pour un champ de saisie (input), sans le symbole de la devise.
 * @param {number | string} amount - Le montant.
 * @param {string} [currencyCode='XOF'] - Le code de la devise pour dterminer le nombre de dcimales.
 * @returns {string}
 *
 * @example
 * formatForInput(1234, 'XOF') // "1234"
 * formatForInput(1234.56, 'EUR') // "1234.56"
 */
export const formatForInput = (amount, currencyCode = 'XOF') => {
    const config = CURRENCY_CONFIG[currencyCode] || CURRENCY_CONFIG.XOF;
    const number = ensureNumber(amount, 0);
    return number.toFixed(config.decimalDigits);
};