// ==============================================================================
//                  Hook Personnalis : useLocalStorage
//
// Ce hook se comporte comme `useState` mais synchronise la valeur avec le
// localStorage du navigateur. Il est idal pour persister des tats simples
// comme les prfrences de l'utilisateur (ex: le thme UI).
//
// Caractristiques :
//   - Persistance des donnes aprs rechargement de la page.
//   - Initialisation "lazy" (via une fonction) pour les calculs coteux.
//   - Synchronisation automatique entre les onglets du navigateur.
// ==============================================================================

import { useState, useEffect, useCallback } from 'react';

export function useLocalStorage(key, initialValue) {
  // `useState` reoit une fonction pour l'initialisation "lazy".
  // Cette fonction ne s'excute qu'une seule fois, au montage du composant.
  const [storedValue, setStoredValue] = useState(() => {
    // Ne pas excuter ct serveur (pour SSR avec Next.js, etc.)
    if (typeof window === 'undefined') {
      return initialValue instanceof Function ? initialValue() : initialValue;
    }

    try {
      const item = window.localStorage.getItem(key);
      // Si une valeur existe dj, on la parse, sinon on utilise la valeur initiale.
      if (item) return JSON.parse(item);
      
      return initialValue instanceof Function ? initialValue() : initialValue;
    } catch (error) {
      console.error(`Erreur lors de la lecture de la cl '${key}' depuis localStorage`, error);
      return initialValue instanceof Function ? initialValue() : initialValue;
    }
  });

  // Wrapper `setValue` pour qu'elle se comporte comme celle de `useState`
  // et mette  jour le localStorage. `useCallback` est utilis pour la performance.
  const setValue = useCallback((value) => {
    try {
      // Permet de passer une fonction pour mettre  jour l'tat (ex: `setCount(c => c + 1)`)
      const valueToStore = value instanceof Function ? value(storedValue) : value;
      // Met  jour l'tat React
      setStoredValue(valueToStore);
      // Met  jour le localStorage
      if (typeof window !== 'undefined') {
        window.localStorage.setItem(key, JSON.stringify(valueToStore));
      }
    } catch (error) {
      console.error(`Erreur lors de la sauvegarde de la cl '${key}' dans localStorage`, error);
    }
  }, [key, storedValue]);

  // coute les changements dans le localStorage depuis d'autres onglets
  useEffect(() => {
    const handleStorageChange = (e) => {
      if (e.key === key && e.newValue !== e.oldValue) {
        try {
          setStoredValue(e.newValue ? JSON.parse(e.newValue) : initialValue);
        } catch (error) {
          console.error(`Erreur lors de la mise  jour depuis l'vnement storage pour la cl '${key}'`, error);
        }
      }
    };

    window.addEventListener('storage', handleStorageChange);
    // Nettoyage de l'couteur d'vnement au dmontage du composant
    return () => {
      window.removeEventListener('storage', handleStorageChange);
    };
  }, [key, initialValue]);


  return [storedValue, setValue];
}