// ==============================================================================
//           Composant Bouton de Bascule du Thme (Clair/Sombre)
//
// Ce composant simple et rutilisable permet  l'utilisateur de basculer
// entre le thme clair et le thme sombre de l'application.
//
// Il utilise le hook `useTheme` pour accder  l'tat du thme et  la
// fonction pour le modifier.
// ==============================================================================

import React from 'react';
import { Button, Tooltip, OverlayTrigger } from 'react-bootstrap';
import { SunFill, MoonStarsFill } from 'react-bootstrap-icons';
import { useTheme } from '../../context/ThemeContext'; // Importe notre hook personnalis

/**
 * Un bouton iconique qui permet de basculer entre le thme clair et sombre,
 * avec une infobulle pour l'accessibilit.
 */
const ThemeToggleButton = (props) => {
  // On consomme le contexte du thme via notre hook personnalis
  const { theme, toggleTheme } = useTheme();

  // Dtermine si le thme actuel est 'light'
  const isLight = theme === 'light';

  // Choisit l'icne et le texte de l'infobulle en fonction du thme
  const Icon = isLight ? MoonStarsFill : SunFill;
  const tooltipText = isLight ? 'Passer au thme sombre' : 'Passer au thme clair';

  // Composant Tooltip de React-Bootstrap
  const renderTooltip = (tooltipProps) => (
    <Tooltip id="theme-tooltip" {...tooltipProps}>
      {tooltipText}
    </Tooltip>
  );

  return (
    <OverlayTrigger
      placement="bottom"
      delay={{ show: 250, hide: 400 }}
      overlay={renderTooltip}
    >
      <Button
        variant="outline-secondary"
        onClick={toggleTheme}
        aria-label="Basculer le thme"
        {...props} // Passe toutes les autres props (comme `className`) au composant Button
      >
        <Icon size={18} />
      </Button>
    </OverlayTrigger>
  );
};

export default ThemeToggleButton;