// ==============================================================================
//           Composant de Route Publique Restreinte
//
// Ce composant est conu pour les routes qui ne devraient tre accessibles
// qu'aux utilisateurs NON authentifis (ex: page de connexion, d'inscription).
//
// Son fonctionnement est l'inverse du `PrivateRoute` :
//   1. Il vrifie l'tat d'authentification de l'utilisateur via le hook `useAuth`.
//   2. Si l'utilisateur est authentifi, il le redirige vers une page
//      par dfaut (comme le tableau de bord).
//   3. Si l'utilisateur n'est pas authentifi, il rend le composant enfant
//      (la page publique, ex: `<LoginPage />`).
// ==============================================================================

import React from 'react';
import { Navigate, Outlet } from 'react-router-dom';
import { useAuth } from '../hooks/useAuth'; // On utilise notre hook personnalis
import Loader from '../components/common/Loader';

/**
 * Un composant qui rend ses enfants uniquement si l'utilisateur N'EST PAS authentifi.
 * Sinon, il redirige vers une page principale de l'application.
 *
 * @param {{redirectTo?: string}} props
 */
const PublicRoute = ({ redirectTo = '/dashboard' }) => {
  // Le hook `useAuth` fournit un tat clair `isAuthenticated` et `isLoading`.
  const { isAuthenticated, isLoading } = useAuth();

  // Pendant que l'on vrifie l'tat d'authentification au premier chargement,
  // on affiche un loader pour viter d'afficher brivement la page publique
  // avant de potentiellement rediriger.
  if (isLoading) {
    return <Loader fullscreen text="Chargement de l'application..." />;
  }

  // Si l'utilisateur est connect, on le redirige loin des pages publiques.
  if (isAuthenticated) {
    return <Navigate to={redirectTo} replace />;
  }

  // Si l'utilisateur n'est pas connect, on affiche la page demande
  // (ex: le formulaire de connexion). `<Outlet />` sera remplac par le
  // composant enfant de la route (<LoginPage />, <RegisterPage />, etc.).
  return <Outlet />;
};

export default PublicRoute;