// ==============================================================================
//                  Fonctions de Formatage des Donnes (Client)
//
// Ce fichier contient des fonctions ddies  la transformation de donnes brutes
// en chanes de caractres formates pour l'affichage dans l'interface utilisateur.
//
// Il garantit la cohrence de la prsentation des dates, devises, nombres, etc.,
//  travers toute l'application, en respectant les conventions locales.
// ==============================================================================

import { formatCurrency as formatCurrencyGeneric } from './currencyUtils';
import { customFormat, parseDateString } from './dateUtils';
import { ensureNumber } from './numberUtils';

/**
 * Formate un montant numrique en devise XOF (Franc CFA).
 * C'est un alias spcifique pour plus de clart dans le code.
 * @param {number | string | null | undefined} amount - Le montant  formater.
 * @returns {string} Le montant format (ex: "1234567FCFA").
 */
export const formatCurrency = (amount) => {
  return formatCurrencyGeneric(amount, 'XOF');
};

/**
 * Formate une date (objet Date ou chane ISO) au format franais court (JJ/MM/AAAA).
 * @param {Date | string | null | undefined} dateInput - La date  formater.
 * @returns {string} La date formate, ou une chane vide si l'entre est invalide.
 */
export const formatDate = (dateInput) => {
  return customFormat(dateInput, 'P'); // 'P' est le format de date localis (dd/MM/yyyy)
};

/**
 * Formate une date pour un affichage plus lisible et complet (ex: "21 mai 2024").
 * @param {Date | string | null | undefined} dateInput - La date  formater.
 * @returns {string} La date formate.
 */
export const formatReadableDate = (dateInput) => {
  return customFormat(dateInput, 'dd MMMM yyyy');
};

/**
 * Formate une date et une heure (ex: "21/05/2024  14:30").
 * @param {Date | string | null | undefined} dateInput - La date  formater.
 * @returns {string} La date et l'heure formates.
 */
export const formatDateTime = (dateInput) => {
  return customFormat(dateInput, "P '' HH:mm");
};

/**
 * Formate un nombre en pourcentage, en le multipliant d'abord par 100 s'il est une fraction.
 * @param {number} value - La valeur numrique (ex: 85 ou 0.85).
 * @param {number} [decimalPlaces=0] - Le nombre de dcimales.
 * @returns {string} La chane de caractres formate (ex: "85 %").
 */
export const formatPercentage = (value, decimalPlaces = 0) => {
  const number = ensureNumber(value);
  
  // Si la valeur est entre 0 et 1, on suppose que c'est une fraction (ex: 0.85 -> 85%)
  const valueToFormat = number <= 1 && number >= 0 ? number : number / 100;

  return new Intl.NumberFormat('fr-FR', {
    style: 'percent',
    minimumFractionDigits: decimalPlaces,
    maximumFractionDigits: decimalPlaces,
  }).format(valueToFormat);
};

/**
 * Tronque une chane de caractres  une longueur maximale et ajoute "...".
 * @param {string} str - La chane  tronquer.
 * @param {number} maxLength - La longueur maximale autorise.
 * @returns {string} La chane tronque ou l'originale si elle est plus courte.
 */
export const truncate = (str, maxLength) => {
    if (typeof str !== 'string' || str.length <= maxLength) {
        return str;
    }
    return `${str.substring(0, maxLength)}...`;
};

/**
 * Formate un statut (chane de caractres) en un format lisible.
 * @param {string} status - Le statut brut (ex: 'partiellement_payee').
 * @returns {string} Le statut format (ex: 'Partiellement Paye').
 */
export const formatStatus = (status) => {
    if (typeof status !== 'string') return '';
    return status
        .replace(/_/g, ' ') // Remplace les underscores par des espaces
        .replace(/\w\S*/g, (txt) => txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase()); // Met en majuscule chaque mot
}