// ==============================================================================
//           Slice Redux pour la Gestion de l'tat de l'Interface Utilisateur (UI)
//
// Rle : Ce slice centralise la gestion des tats de l'UI non lis aux donnes,
// tels que l'tat du menu, le thme, ou les indicateurs de chargement globaux.
// Il assure une sparation nette entre l'tat de l'application et l'tat de l'UI.
//
// Bonnes Pratiques Implmentes :
// - Persistance du thme dans le localStorage.
// - Dtection du thme prfr de l'utilisateur.
// - Reducers atomiques et clairs.
// ==============================================================================

import { createSlice } from '@reduxjs/toolkit';
import { LOCAL_STORAGE_KEYS } from '../../utils/constants';

/**
 * Dtermine le thme initial de l'application.
 * Priorit : Thme sauvegard > Prfrence systme > Thme par dfaut ('light').
 * @returns {'light' | 'dark'} Le thme  utiliser.
 */
const getInitialTheme = () => {
  try {
    const savedTheme = localStorage.getItem(LOCAL_STORAGE_KEYS.UI_THEME);
    if (savedTheme === 'light' || savedTheme === 'dark') {
      return savedTheme;
    }

    // Si aucun thme n'est sauvegard, on se fie aux prfrences de l'OS/navigateur.
    if (window.matchMedia?.('(prefers-color-scheme: dark)').matches) {
      return 'dark';
    }
  } catch (error) {
    console.error("Impossible d'accder au localStorage pour le thme :", error);
  }
  return 'light'; // Thme par dfaut
};

// --- tat Initial ---
const initialState = {
  theme: getInitialTheme(),
  isSidebarOpen: false, // Le menu est ferm par dfaut sur les petits crans
  globalLoading: false, // Indicateur pour un loader global (ex: au chargement initial)
};


// --- Cration du Slice ---
export const uiSlice = createSlice({
  name: 'ui',
  initialState,
  
  // Les reducers dfinissent comment l'tat peut tre modifi de manire synchrone.
  reducers: {
    /**
     * Bascule l'tat d'ouverture du menu latral (sidebar).
     */
    toggleSidebar: (state) => {
      state.isSidebarOpen = !state.isSidebarOpen;
    },
    
    /**
     * Force l'ouverture du menu latral.
     */
    openSidebar: (state) => {
      state.isSidebarOpen = true;
    },

    /**
     * Force la fermeture du menu latral.
     */
    closeSidebar: (state) => {
      state.isSidebarOpen = false;
    },

    /**
     * Change le thme de l'application ('light' ou 'dark').
     * @param {object} action - L'action contenant le nouveau thme dans `action.payload`.
     */
    setTheme: (state, action) => {
      const newTheme = action.payload === 'dark' ? 'dark' : 'light';
      state.theme = newTheme;
      // La persistance est gre directement ici pour une meilleure encapsulation.
      try {
        localStorage.setItem(LOCAL_STORAGE_KEYS.UI_THEME, newTheme);
      } catch (error) {
        console.error("Impossible de sauvegarder le thme dans le localStorage :", error);
      }
    },
    
    /**
     * Active ou dsactive l'indicateur de chargement global.
     * @param {object} action - L'action o `action.payload` est un boolen.
     */
    setGlobalLoading: (state, action) => {
        state.globalLoading = !!action.payload; // Assure que la valeur est un boolen
    }
  },
});

// --- Exportation des Actions ---
// Ces "action creators" sont gnrs automatiquement par `createSlice`.
export const {
  toggleSidebar,
  openSidebar,
  closeSidebar,
  setTheme,
  setGlobalLoading,
} = uiSlice.actions;


// --- Exportation des Slecteurs (Bonne Pratique de Performance) ---
// Les slecteurs permettent d'extraire des donnes du store.
// En les dfinissant ici, on centralise la logique d'accs  l'tat.
export const selectTheme = (state) => state.ui.theme;
export const selectIsSidebarOpen = (state) => state.ui.isSidebarOpen;
export const selectIsGlobalLoading = (state) => state.ui.globalLoading;


// --- Exportation du Reducer ---
// Le reducer sera ajout au `rootReducer` global.
export default uiSlice.reducer;