// ==============================================================================
//           Combinaison des Reducers de l'Application (Root Reducer)
//
// Ce fichier utilise la fonction `combineReducers` de Redux Toolkit pour
// assembler tous les reducers de l'application (provenant des diffrents "slices")
// en un seul "reducer racine".
//
// C'est ce reducer racine qui dfinit la structure globale de l'tat Redux.
// Chaque cl de l'objet correspondra  une tranche ("slice") de l'tat global.
//
// Exemple de l'tat global rsultant :
// {
//   auth: { user: null, token: null, ... },
//   ui: { isSidebarOpen: true, ... },
//   clients: { list: [], loading: false, ... }
// }
// ==============================================================================

import { combineReducers } from '@reduxjs/toolkit';

// --- Importation des reducers de chaque slice ---
// Au fur et  mesure que vous crerez de nouveaux slices, vous les importerez et
// les ajouterez  l'objet `combineReducers` ci-dessous.

import authReducer from './slices/authSlice';
import uiReducer from './slices/uiSlice'; //  crer
import dashboardReducer from './slices/dashboardSlice'; //  crer
import clientsReducer from './slices/clientsSlice'; //  crer
// import produitsReducer from './slices/produitsSlice';
// ... etc.

/**
 * Combine tous les reducers en un seul objet.
 * La cl de chaque champ dfinit le nom de la tranche dans l'tat global Redux,
 * accessible via `useSelector(state => state.nomDeLaTranche)`.
 */
const rootReducer = combineReducers({
  auth: authReducer,
  ui: uiReducer,
  dashboard: dashboardReducer,
  clients: clientsReducer,
  // ... Ajoutez vos autres reducers ici au fur et  mesure
});

export default rootReducer;