// ==============================================================================
//           Contexte React pour la Gestion du Thme (Clair/Sombre)
//
// Ce module fournit un Contexte et un Provider pour grer le thme de l'interface
// utilisateur de manire globale dans toute l'application.
// Il gre la persistance du choix de l'utilisateur et dtecte les prfrences
// du systme pour le premier chargement.
// ==============================================================================
import React, { createContext, useContext, useEffect, useCallback, useMemo } from 'react';
import { useLocalStorage } from '../hooks/useLocalStorage';
import { LOCAL_STORAGE_KEYS } from '../utils/constants';

// 1. Cration du Contexte
// La valeur par dfaut `null` sera surcharge par le Provider.
const ThemeContext = createContext(null);

/**
 * Fonction d'aide pour dterminer le thme initial au premier chargement.
 * Elle est conue pour ne s'excuter qu'une seule fois.
 */
const getInitialTheme = () => {
  // Si le code s'excute ct serveur (SSR), on retourne une valeur par dfaut.
  if (typeof window === 'undefined') {
    return 'light';
  }

  // Priorit 1 : Vrifier s'il y a un thme dj sauvegard par l'utilisateur.
  const savedTheme = window.localStorage.getItem(LOCAL_STORAGE_KEYS.UI_THEME);
  if (savedTheme === 'light' || savedTheme === 'dark') {
    return savedTheme;
  }

  // Priorit 2 : Vrifier les prfrences du systme d'exploitation / navigateur.
  if (window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches) {
    return 'dark';
  }
  
  // Priorit 3 : Thme par dfaut.
  return 'light';
};

/**
 * Le Provider (fournisseur) qui va envelopper notre application pour donner
 * accs au contexte du thme  tous ses enfants.
 * @param {{children: React.ReactNode}} props
 */
export const ThemeProvider = ({ children }) => {
  // On utilise notre hook `useLocalStorage` avec la fonction d'initialisation lazy.
  const [theme, setTheme] = useLocalStorage(LOCAL_STORAGE_KEYS.UI_THEME, getInitialTheme);

  // Applique le thme au document  chaque changement de la variable `theme`.
  useEffect(() => {
    const root = window.document.documentElement;
    
    // On met  jour l'attribut `data-theme` qui contrle nos variables CSS.
    root.setAttribute('data-theme', theme);
  }, [theme]);

  // `useCallback` mmorise la fonction `toggleTheme` pour qu'elle ne soit
  // pas recre  chaque re-render, ce qui optimise les performances.
  const toggleTheme = useCallback(() => {
    setTheme(prevTheme => (prevTheme === 'light' ? 'dark' : 'light'));
  }, [setTheme]);

  // `useMemo` mmorise l'objet `value` du contexte. Les composants qui
  // consomment ce contexte ne se re-rendront que si `theme` ou `toggleTheme`
  // changent rellement.
  const value = useMemo(() => ({
    theme,
    toggleTheme,
  }), [theme, toggleTheme]);

  return (
    <ThemeContext.Provider value={value}>
      {children}
    </ThemeContext.Provider>
  );
};

/**
 * Hook personnalis pour consommer facilement le Contexte du Thme.
 * @returns {{theme: ('light' | 'dark'), toggleTheme: function}}
 */
export const useTheme = () => {
  const context = useContext(ThemeContext);
  if (context === undefined) {
    throw new Error("useTheme doit tre utilis  l'intrieur d'un ThemeProvider");
  }
  return context;
};