// ==============================================================================
//                  Fonctions de Validation pour Formulaires (Client)
//
// Ce fichier contient des fonctions de validation conues pour tre utilises
// dans les formulaires ct client (avec des bibliothques comme Formik,
// React Hook Form, ou des formulaires contrls simples).
//
// Leur but est de fournir un retour instantan  l'utilisateur.
// Ces validations sont une premire ligne de dfense et n'excluent PAS
// les validations robustes qui doivent tre effectues sur le backend.
// ==============================================================================

/**
 * Vrifie si une valeur est vide (chane vide, null, undefined).
 * @param {*} value - La valeur  vrifier.
 * @returns {string | undefined} Un message d'erreur si la valeur est vide, sinon undefined.
 */
export const isRequired = (value) => {
  return value && String(value).trim().length > 0
    ? undefined
    : 'Ce champ est obligatoire.';
};

/**
 * Valide un format d'adresse email.
 * @param {string} email - L'email  valider.
 * @returns {string | undefined} Un message d'erreur si l'email est invalide.
 */
export const isValidEmail = (email) => {
  if (!email) return undefined; // La validation 'isRequired' s'en chargera
  const emailRegex = /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,4})+$/;
  return emailRegex.test(email)
    ? undefined
    : "Le format de l'adresse email est invalide.";
};

/**
 * Valide la longueur minimale d'une chane de caractres.
 * @param {number} min - La longueur minimale.
 * @returns {function(*): (string | undefined)} Une fonction de validation.
 */
export const minLength = (min) => (value) => {
  return value && value.length >= min
    ? undefined
    : `Ce champ doit contenir au moins ${min} caractres.`;
};

/**
 * Valide la force d'un mot de passe de manire dtaille.
 * @param {string} password - Le mot de passe  valider.
 * @returns {string | undefined} Un message d'erreur si le mot de passe n'est pas assez fort.
 */
export const isStrongPassword = (password) => {
  if (!password) return undefined;
  const hasUpperCase = /[A-Z]/.test(password);
  const hasLowerCase = /[a-z]/.test(password);
  const hasNumber = /[0-9]/.test(password);
  const hasSpecialChar = /[!@#$%^&*]/.test(password);

  if (password.length < 8) return 'Doit contenir au moins 8 caractres.';
  if (!hasUpperCase) return 'Doit contenir au moins une majuscule.';
  if (!hasLowerCase) return 'Doit contenir au moins une minuscule.';
  if (!hasNumber) return 'Doit contenir au moins un chiffre.';
  if (!hasSpecialChar) return 'Doit contenir au moins un caractre spcial (!@#$%^&*).';

  return undefined;
};

/**
 * Valide un numro de tlphone sngalais.
 * @param {string} phone - Le numro  valider.
 * @returns {string | undefined} Un message d'erreur si invalide.
 */
export const isValidSenegalPhone = (phone) => {
  if (!phone) return undefined;
  const phoneStr = String(phone).replace(/\s+/g, '');
  const validPrefixes = ['77', '78', '76', '70', '75'];
  const phoneRegex = new RegExp(`^(${validPrefixes.join('|')})\\d{7}$`);

  return phoneRegex.test(phoneStr)
    ? undefined
    : 'Doit tre un numro sngalais valide (ex: 77 123 45 67).';
};

/**
 * Vrifie si une valeur est un nombre positif.
 * @param {*} value - La valeur  vrifier.
 * @returns {string | undefined} Un message d'erreur si invalide.
 */
export const isPositiveNumber = (value) => {
    const num = Number(String(value).replace(',', '.')); // Gre la virgule
    return !isNaN(num) && num > 0
        ? undefined
        : 'La valeur doit tre un nombre suprieur  zro.'
}

/**
 * Compose plusieurs fonctions de validation en une seule.
 * Excute les validateurs en squence et retourne le premier message d'erreur trouv.
 * @param  {...function} validators - Les fonctions de validation  composer.
 * @returns {function(*): (string | undefined)} Une unique fonction de validation.
 */
export const composeValidators = (...validators) => (value) => {
  return validators.reduce((error, validator) => error || validator(value), undefined);
};