// ==============================================================================
//           Slice Redux pour la Gestion de l'tat du Tableau de Bord
//
// Ce slice est responsable de l'tat des donnes affiches sur le tableau de
// bord principal.
//
// Il utilise un thunk unique `fetchDashboardData` pour rcuprer toutes les
// donnes en un seul appel API, pour des performances de chargement optimales.
// ==============================================================================

import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import dashboardService from '../../services/dashboardService';
import { getErrorMessage } from '../../utils/helpers';
import { REDUX_SLICE_NAMES } from '../../utils/constants';

// --- tat Initial ---
// L'tat initial est plus simple. Il contient des valeurs par dfaut
// pour les donnes que les composants attendent.
const initialState = {
  status: 'idle', // 'idle' | 'loading' | 'succeeded' | 'failed'
  error: null,
  // Les donnes seront peuples par l'API. On peut garder des valeurs par dfaut
  // pour viter les erreurs `undefined` dans les composants au premier rendu.
  kpis: null,
  salesChart: null,
  previsionnelTresorerie: null,
  dernieresFacturesEnRetard: [],
};


// --- Thunk Asynchrone Unique ---

/**
 * Thunk pour rcuprer toutes les donnes du tableau de bord principal.
 */
export const fetchDashboardData = createAsyncThunk(
  `${REDUX_SLICE_NAMES.DASHBOARD}/fetchAll`,
  async (_, thunkAPI) => {
    try {
      // Appelle la fonction optimise du service qui fait un seul appel API.
      const response = await dashboardService.getMainDashboardData();
      // Le service retourne dj la partie `data` de la rponse Axios.
      return response.data;
    } catch (error) {
      const message = getErrorMessage(error);
      return thunkAPI.rejectWithValue(message);
    }
  }
);


// --- Cration du Slice ---

export const dashboardSlice = createSlice({
  name: REDUX_SLICE_NAMES.DASHBOARD,
  initialState,
  reducers: {
    // Action pour rinitialiser l'tat du dashboard, utile lors de la dconnexion
    resetDashboard: () => initialState,
  },
  extraReducers: (builder) => {
    builder
      .addCase(fetchDashboardData.pending, (state) => {
        state.status = 'loading';
        state.error = null;
      })
      .addCase(fetchDashboardData.fulfilled, (state, action) => {
        state.status = 'succeeded';
        // Mise  jour simplifie : on fusionne l'objet `data` reu de l'API
        // directement dans notre tat. Si l'API ajoute une nouvelle cl, elle
        // sera automatiquement disponible dans le state.
        Object.assign(state, action.payload);
      })
      .addCase(fetchDashboardData.rejected, (state, action) => {
        state.status = 'failed';
        state.error = action.payload; // Le message d'erreur est dans le payload
        // On rinitialise les donnes en cas d'erreur pour viter d'afficher des donnes obsoltes.
        state.kpis = null;
        state.salesChart = null;
        state.previsionnelTresorerie = null;
        state.dernieresFacturesEnRetard = [];
      });
  },
});

export const { resetDashboard } = dashboardSlice.actions;
export default dashboardSlice.reducer;