import React from 'react';
import { Link } from 'react-router-dom';
import './NotFound.css';

// Importation d'une icne pertinente depuis une bibliothque comme React Icons
import { FiAlertTriangle } from 'react-icons/fi';

const NotFound = () => {
  return (
    <div className="not-found-container">
      <div className="not-found-content">
        <FiAlertTriangle className="not-found-icon" />
        <h1 className="not-found-title">Erreur 404 | Page Introuvable</h1>
        <p className="not-found-message">
          Dsol, la page que vous cherchez n'a pas pu tre trouve. Il est possible que l'adresse ait t mal saisie ou que la page ait t dplace.
        </p>
        <p className="not-found-suggestion">
          Voici quelques liens qui pourraient vous tre utiles :
        </p>
        <div className="not-found-links">
          <Link to="/dashboard" className="not-found-link">
            Retour au Tableau de Bord
          </Link>
          <Link to="/support" className="not-found-link">
            Contacter le Support
          </Link>
          <Link to="/" className="not-found-link">
            Page d'Accueil
          </Link>
        </div>
      </div>
    </div>
  );
};

export default NotFound;