// ==============================================================================
//           Configuration du Store Redux Principal
//
// Ce fichier est le cur de la gestion d'tat de l'application.
// Il configure et cre le store Redux en utilisant Redux Toolkit.
//
// Il assemble les reducers, ajoute les middlewares ncessaires, et injecte
// l'instance du store dans le client API pour rsoudre les dpendances circulaires.
// ==============================================================================

import { configureStore } from '@reduxjs/toolkit';
import rootReducer from './rootReducer';

// On importe la fonction d'injection depuis notre client API
import { injectStore } from '../services/api';

// --- Importation des Middlewares (Optionnel selon l'architecture choisie) ---
// import apiMiddleware from './middleware/apiMiddleware';
// import authMiddleware from './middleware/authMiddleware';

/**
 * Cration et configuration du store Redux.
 */
const store = configureStore({
  // `rootReducer` est la combinaison de tous les reducers de nos slices.
  reducer: rootReducer,
  
  // `middleware` est l'endroit o l'on ajoute nos middlewares personnaliss.
  // `getDefaultMiddleware` inclut dj des outils utiles comme `redux-thunk`.
  middleware: (getDefaultMiddleware) => 
    getDefaultMiddleware({
      // Il est recommand de garder la vrification de srialisabilit active,
      // mais elle peut tre dsactive si vous avez des raisons spcifiques
      // de stocker des valeurs non-srialisables (comme des Dates).
      serializableCheck: false,
    }),
    // Exemple si vous utilisiez des middlewares personnaliss :
    // .concat(apiMiddleware, authMiddleware),

  // Activer les Redux DevTools uniquement en environnement de dveloppement
  // pour des raisons de performance et de scurit.
  devTools: process.env.NODE_ENV !== 'production',
});


// ==============================================================================
// ---                       INJECTION DE DPENDANCE                        ---
// ==============================================================================
// C'est l'tape cruciale.
// On injecte le store qui vient d'tre cr dans le module `api.js`.
// L'intercepteur Axios aura maintenant accs  `store.dispatch` et `store.getState`
// pour grer la dconnexion automatique en cas d'erreur 401.
injectStore(store);

export default store;

// --- Infrence des types pour TypeScript (Optionnel mais recommand) ---
// export type RootState = ReturnType<typeof store.getState>;
// export type AppDispatch = typeof store.dispatch;