// ==============================================================================
//           Composant Graphique Linaire (LineChart)
//
// Ce composant est un "wrapper" autour de `react-chartjs-2` pour afficher
// des graphiques linaires de manire cohrente et simple.
//
// Il configure des options par dfaut pour un design propre et intgre le
// plugin 'Filler' pour permettre le remplissage de couleur sous la courbe.
// ==============================================================================

import React from 'react';
import { Line } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  Filler, // Importer le plugin 'Filler'
} from 'chart.js';

// --- Enregistrement des composants Chart.js ---
// Il est crucial d'enregistrer tous les lments que vous comptez utiliser.
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  Filler // Enregistrer le plugin ici
);


/**
 * Affiche un graphique linaire configurable.
 *
 * @param {{
 *  data: import('chart.js').ChartData<'line'>,
 *  options?: import('chart.js').ChartOptions<'line'>
 * }} props
 */
const LineChart = ({ data, options }) => {

  const defaultOptions = {
    responsive: true,
    maintainAspectRatio: false, // Important pour contrler la hauteur via CSS
    plugins: {
      legend: {
        position: 'top',
        labels: {
          color: '#6c757d', // Couleur de la lgende ( adapter au thme)
        },
      },
      title: {
        display: false, // Le titre est souvent gr par le composant parent (ex: Card.Header)
      },
      tooltip: {
        mode: 'index',
        intersect: false,
        backgroundColor: '#343a40',
      },
    },
    scales: {
      x: {
        grid: {
          display: false, // Masquer la grille verticale
        },
        ticks: {
          color: '#6c757d',
        },
      },
      y: {
        grid: {
          color: '#e9ecef', // Couleur de la grille horizontale ( adapter au thme)
        },
        ticks: {
          color: '#6c757d',
          // Optionnel : formater les ticks de l'axe Y en devise
          // callback: function(value) {
          //   return new Intl.NumberFormat('fr-SN', { style: 'currency', currency: 'XOF', notation: 'compact' }).format(value);
          // }
        },
      },
    },
    elements: {
      line: {
        tension: 0.4, // Pour des courbes lisses
      },
    },
  };

  // Fusionne les options par dfaut avec celles passes en props.
  const chartOptions = { ...defaultOptions, ...options };

  return <Line options={chartOptions} data={data} />;
};

export default LineChart;