// ==============================================================================
//           Composant de Route Protge (PrivateRoute)
//
// Ce composant est un "wrapper" autour des routes qui ncessitent une
// authentification. Il agit comme un gardien.
//
// Son fonctionnement :
//   1. Il utilise le hook `useAuth` pour vrifier l'tat d'authentification.
//   2. Si l'utilisateur est authentifi (`isAuthenticated` est vrai), il rend
//      le composant enfant (la page protge) via `<Outlet>`.
//   3. Si l'utilisateur n'est pas authentifi, il le redirige vers la
//      page de connexion (`/login`).
//
// Il utilise le composant `Navigate` de `react-router-dom` pour la redirection.
// ==============================================================================

import React from 'react';
import { Navigate, Outlet } from 'react-router-dom';
import { useAuth } from '../hooks/useAuth'; // On utilise notre hook personnalis
import Loader from '../components/common/Loader';

/**
 * Un composant qui rend ses enfants uniquement si l'utilisateur est authentifi.
 * Sinon, il redirige vers la page de connexion.
 *
 * @param {{redirectTo?: string}} props
 */
const PrivateRoute = ({ redirectTo = '/login' }) => {
  // Le hook `useAuth` nous donne un tat clair `isAuthenticated` et `isLoading`.
  // Ce composant n'a plus besoin de connatre la structure interne de Redux (token, etc.).
  const { isAuthenticated, isLoading } = useAuth();

  // Pendant que l'tat d'authentification est en cours de vrification
  // (par exemple, au premier chargement de l'application), on affiche un loader.
  // Cela vite un "flash" o l'utilisateur est brivement redirig vers /login
  // avant que son tat de connexion ne soit confirm.
  if (isLoading) {
    return <Loader fullscreen text="Vrification de la session..." />;
  }
  
  // Si l'utilisateur est authentifi, on rend la page demande.
  // `<Outlet />` est un placeholder de react-router-dom qui sera remplac
  // par le composant enfant de la route (ex: <DashboardPage />).
  if (isAuthenticated) {
    return <Outlet />;
  }

  // Si l'utilisateur n'est pas authentifi, on le redirige.
  // L'option `replace` remplace l'entre actuelle dans l'historique de navigation,
  // ce qui empche l'utilisateur de revenir  la page protge avec le
  // bouton "prcdent" du navigateur aprs avoir t redirig.
  return <Navigate to={redirectTo} replace />;
};

export default PrivateRoute;