// ==============================================================================
//           Composant Carte de Statistique (StatCard)
//
// Ce composant rutilisable est conu pour afficher un indicateur de
// performance cl (KPI) sur un tableau de bord.
//
// Il combine une icne, une valeur principale, un titre et une variation
// optionnelle pour une prsentation claire et impactante des donnes.
// ==============================================================================

import React from 'react';
import { Card, Spinner } from 'react-bootstrap';
import { ArrowDownRight, ArrowUpRight } from 'react-bootstrap-icons';

/**
 * Affiche une carte de statistique (KPI) pour un tableau de bord.
 *
 * @param {{
 *  icon: React.ElementType,
 *  value: string | number,
 *  title: string,
 *  iconVariant?: 'primary' | 'success' | 'warning' | 'danger' | 'info',
 *  isLoading?: boolean,
 *  formatter?: (value: string | number) => string,
 *  change?: {
 *    value: number,
 *    direction: 'increase' | 'decrease'
 *  }
 * }} props
 */
const StatCard = ({
  icon: Icon, // Renomme la prop `icon` en `Icon` pour l'utiliser comme un composant
  value,
  title,
  iconVariant = 'primary',
  isLoading = false,
  formatter,
  change,
}) => {

  /**
   * Rend le petit indicateur de variation (hausse ou baisse).
   */
  const renderChange = () => {
    if (isLoading || !change || typeof change.value === 'undefined') {
      return null;
    }

    const isIncrease = change.direction === 'increase';
    const changeColor = isIncrease ? 'text-success' : 'text-danger';
    const ChangeIcon = isIncrease ? ArrowUpRight : ArrowDownRight;

    return (
      <span className={`ms-2 small fw-bold ${changeColor}`}>
        <ChangeIcon size={12} /> {change.value}%
      </span>
    );
  };

  return (
    <Card className="h-100 shadow-sm border-0">
      <Card.Body>
        <div className="d-flex align-items-center">
          {/* L'icne style */}
          <div className={`stat-card-icon bg-${iconVariant}-light text-${iconVariant} rounded-circle me-3`}>
            <Icon size={24} />
          </div>
          
          {/* Les informations textuelles */}
          <div className="stat-card-info flex-grow-1">
            <h6 className="text-muted fw-normal mb-1">{title}</h6>
            <div className="d-flex align-items-baseline">
              {isLoading ? (
                <Spinner animation="border" size="sm" variant={iconVariant} />
              ) : (
                <h3 className="fw-bold mb-0">
                  {formatter ? formatter(value) : value}
                </h3>
              )}
              {renderChange()}
            </div>
          </div>
        </div>
      </Card.Body>
    </Card>
  );
};

export default StatCard;