// ==============================================================================
//                Composant En-tte de l'Application (Header)
//
// Rle : Affiche la barre de navigation suprieure. Contient un bouton pour
// basculer le menu latral sur mobile, des liens rapides (notifications),
// le bouton de changement de thme, et un menu droulant pour l'utilisateur.
// ==============================================================================

import React from 'react';
import { useDispatch } from 'react-redux';
import { Link } from 'react-router-dom';
import { Navbar, Nav, Dropdown, Image, Button } from 'react-bootstrap';
import { PersonCircle, BellFill, GearFill, BoxArrowLeft, List } from 'react-bootstrap-icons';

// --- Importations personnalises ---
import { useAuth } from '../../hooks/useAuth';
import { usePermissions } from '../../hooks/usePermissions'; // Utilise le hook de permissions
import { logout } from '../../store/slices/authSlice';
import ThemeToggleButton from './ThemeToggleButton';

/**
 * Sous-composant pour un item de menu droulant qui ne s'affiche que si
 * l'utilisateur a la permission requise.
 * @param {{
 *  requiredPermission: string,
 *  children: React.ReactNode
 * }} props
 */
const ProtectedDropdownItem = ({ requiredPermission, children }) => {
  const { hasPermission } = usePermissions(); // Utilise le hook pour une API plus simple

  if (hasPermission(requiredPermission)) {
    return children;
  }
  return null;
};


// ==========================================================
// --- Composant Principal Header ---
// ==========================================================
/**
 * @param {{
 *  onToggleSidebar: () => void
 * }} props
 */
const Header = ({ onToggleSidebar }) => {
  const { user } = useAuth();
  const dispatch = useDispatch();

  const handleLogout = () => {
    dispatch(logout());
    // La redirection est gre par le composant PrivateRoute qui ragit
    // au changement d'tat de l'authentification.
  };

  return (
    <Navbar expand="lg" className="header sticky-top shadow-sm px-3" as="header">
      {/* Bouton pour afficher/masquer le menu latral sur mobile */}
      <Button
        variant="outline-secondary"
        onClick={onToggleSidebar}
        className="d-lg-none me-2 border-0" // Rendu invisible sur les grands crans
        aria-label="Toggle navigation"
      >
        <List size={24} />
      </Button>

      {/* Barre de recherche (Placeholder) */}
      <div className="d-none d-md-block ms-3">
        {/* <Form.Control type="search" placeholder="Rechercher..." /> */}
      </div>
      
      {/* On pousse le contenu suivant vers la droite */}
      <Nav className="ms-auto d-flex flex-row align-items-center">
        
        {/* Bouton de changement de thme */}
        <div className="me-3">
          <ThemeToggleButton />
        </div>

        {/* Icne de Notifications (exemple) */}
        <Nav.Link as={Link} to="/notifications" className="me-3" title="Notifications">
          <BellFill size={20} />
          {/* Un badge pourrait tre ajout ici dynamiquement */}
        </Nav.Link>

        {/* Menu droulant de l'utilisateur */}
        <Dropdown as={Nav.Item} align="end">
          <Dropdown.Toggle as={Nav.Link} className="p-0" id="user-dropdown-toggle">
            {/* Affiche l'avatar de l'utilisateur ou une icne par dfaut */}
            {user?.avatarUrl ? (
              <Image src={user.avatarUrl} roundedCircle width={36} height={36} className="avatar" />
            ) : (
              <PersonCircle size={36} />
            )}
          </Dropdown.Toggle>

          <Dropdown.Menu className="mt-2 shadow-lg">
            <Dropdown.Header>
              Connect en tant que<br />
              <strong>{user?.firstName} {user?.lastName}</strong><br />
              <small className="text-muted">{user?.role}</small>
            </Dropdown.Header>
            <Dropdown.Divider />
            
            <Dropdown.Item as={Link} to="/profil">
              <PersonCircle className="me-2" />
              Mon Profil
            </Dropdown.Item>

            <ProtectedDropdownItem requiredPermission="settings:manage">
              <Dropdown.Item as={Link} to="/parametres">
                <GearFill className="me-2" />
                Paramtres
              </Dropdown.Item>
            </ProtectedDropdownItem>
            
            <Dropdown.Divider />

            <Dropdown.Item onClick={handleLogout} className="text-danger">
              <BoxArrowLeft className="me-2" />
              Dconnexion
            </Dropdown.Item>
          </Dropdown.Menu>
        </Dropdown>
      </Nav>
    </Navbar>
  );
};

export default Header;