// ==============================================================================
//           Composant Racine de l'Application (App.jsx)
//
// Ce fichier est le point d'entre principal de l'interface React.
// Son unique responsabilit est d'assembler les "Providers" de contexte
// globaux et le systme de routage de l'application.
//
// L'ordre des Providers est important : les hooks utiliss dans un Provider
// doivent provenir d'un Provider qui l'englobe.
// ==============================================================================

import React from 'react';
import { BrowserRouter as Router } from 'react-router-dom';
import { Provider as ReduxProvider } from 'react-redux';

// --- Importation des contextes et du store ---
import store from './store';
import { ThemeProvider } from './context/ThemeContext';
import { NotificationProvider } from './context/NotificationContext';
import { SocketProvider } from './context/SocketContext';
import AppRoutes from './routes/routes'; // Le composant qui gre toutes les routes

/**
 * Le composant principal de l'application.
 */
function App() {
  return (
    // 1. Fournisseur Redux : Rend le store disponible  toute l'application.
    <ReduxProvider store={store}>
      {/* 2. Fournisseur de Thme : Gre le thme clair/sombre. */}
      <ThemeProvider>
        {/* 3. Fournisseur de Notifications : Gre l'affichage des toasts. */}
        <NotificationProvider>
          {/* 4. Fournisseur de Socket : Gre la connexion WebSocket.
                 Il est  l'intrieur de ReduxProvider pour pouvoir lire le token. */}
          <SocketProvider>
            {/* 5. Fournisseur de Routage : Active la navigation ct client. */}
            <Router>
              {/* 6. Le routeur de l'application qui affiche la page approprie. */}
              <AppRoutes />
            </Router>
          </SocketProvider>
        </NotificationProvider>
      </ThemeProvider>
    </ReduxProvider>
  );
}

export default App;