// ==============================================================================
//           Page Principale du Tableau de Bord
//
// Rle : Affiche une synthse visuelle de l'activit commerciale. Elle rcupre
// ses donnes depuis le store Redux et les affiche  l'aide de composants
// de visualisation rutilisables.
// ==============================================================================

import React, { useEffect, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Row, Col, Card } from 'react-bootstrap';

// --- Importations internes ---
import { useAuth } from '../../hooks/useAuth';
import { fetchDashboardData } from '../../store/slices/dashboardSlice';
import StatCard from '../../components/charts/StatCard';
import LineChart from '../../components/charts/LineChart';
import BarChart from '../../components/charts/BarChart';
import Loader from '../../components/common/Loader';
import { formatCurrency } from '../../utils/formatters';
import { Cart4, People, JournalText, Wallet2 } from 'react-bootstrap-icons';

const DashboardPage = () => {
  const dispatch = useDispatch();
  const { user } = useAuth();

  // On rcupre l'tat complet du slice `dashboard`.
  const { kpis, salesChart, dernieresFacturesEnRetard, status, error } = useSelector(
    (state) => state.dashboard
  );
  
  // Se dclenche une fois au montage pour charger les donnes.
  useEffect(() => {
    if (status === 'idle') {
      dispatch(fetchDashboardData());
    }
  }, [status, dispatch]);

  // --- Transformation des donnes pour les graphiques avec `useMemo` ---
  const salesChartData = useMemo(() => {
    if (!salesChart?.labels || salesChart.labels.length === 0) {
      return { labels: [], datasets: [] };
    }
    return {
      labels: salesChart.labels,
      datasets: [{
        label: "Chiffre d'Affaires (FCFA)",
        data: salesChart.datasets?.[0]?.data || [],
        borderColor: 'rgb(30, 64, 175)',
        backgroundColor: 'rgba(30, 64, 175, 0.2)',
        fill: true,
        tension: 0.4,
      }],
    };
  }, [salesChart]);
  
  // Donnes factices ( remplacer par les donnes du store quand l'API les fournira)
  const topProductsData = useMemo(() => ({
      labels: ['Produit A', 'Produit B', 'Service C', 'Produit D'],
      datasets: [{
          label: 'Ventes (units)',
          data: [300, 150, 220, 180],
          backgroundColor: 'rgba(22, 163, 74, 0.6)',
          borderRadius: 4,
      }]
  }), []);

  // --- Gestion des diffrents tats de l'UI ---
  if (status === 'loading' && !kpis) {
    return <Loader centered showText text="Chargement du tableau de bord..." />;
  }

  if (status === 'failed') {
    return <div className="alert alert-danger">Erreur de chargement du tableau de bord : {error}</div>;
  }

  return (
    <div>
      <div className="mb-4">
        <h1 className="h2 mb-0">Bonjour, {user?.firstName || 'Utilisateur'} !</h1>
        <p className="text-muted">Voici un aperu de votre activit.</p>
      </div>

      {/* --- Section des KPIs --- */}
      {/* On utilise l'optional chaining (?.) partout pour viter les erreurs si l'objet kpis est null */}
      <Row className="g-4 mb-4">
        <Col md={6} xl={3}>
          <StatCard
            icon={Cart4}
            title="CA du Jour"
            value={kpis?.caDuJour?.value ?? 0}
            change={kpis?.caDuJour?.change}
            formatter={formatCurrency}
            iconVariant="primary"
            isLoading={status === 'loading'}
          />
        </Col>
        <Col md={6} xl={3}>
          <StatCard
            icon={People}
            title="Nouveaux Clients (Jour)"
            value={kpis?.nouveauxClients?.value ?? 0}
            change={kpis?.nouveauxClients?.change}
            iconVariant="success"
            isLoading={status === 'loading'}
          />
        </Col>
        <Col md={6} xl={3}>
          <StatCard
            icon={JournalText}
            title="Devis en Attente"
            value={kpis?.devisEnAttente?.value ?? 0}
            iconVariant="warning"
            isLoading={status === 'loading'}
          />
        </Col>
        <Col md={6} xl={3}>
            {/* Ce KPI viendra du tresorerieService, pour l'instant on le met en dur */}
            <StatCard
            icon={Wallet2}
            title="Paiements en Retard"
            value={dernieresFacturesEnRetard?.length ?? 0}
            iconVariant="danger"
            isLoading={status === 'loading'}
          />
        </Col>
      </Row>

      {/* --- Section des Graphiques --- */}
      <Row className="g-4">
        <Col lg={8}>
          <Card className="shadow-sm border-0 h-100">
            <Card.Body>
              <h5 className="card-title">volution des Ventes (12 derniers mois)</h5>
              <div style={{ height: '350px' }}>
                <LineChart data={salesChartData} />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col lg={4}>
          <Card className="shadow-sm border-0 h-100">
            <Card.Body>
              <h5 className="card-title">Top Produits</h5>
              <div style={{ height: '350px' }}>
                <BarChart data={topProductsData} options={{ indexAxis: 'y', plugins: { legend: { display: false } } }} />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </div>
  );
};

export default DashboardPage;