// ==============================================================================
//                  Hook Personnalis : useAuth
//
// Ce hook agit comme une "faade" pour le slice d'authentification de Redux.
// Il fournit un accs simple et centralis  l'tat d'authentification
// (utilisateur, token, tat de chargement) et  des valeurs drives
// pratiques comme `isAuthenticated`.
//
// Cela permet aux composants de consommer l'tat d'auth sans connatre
// la structure interne du store Redux.
// ==============================================================================

import { useMemo } from 'react';
import { useSelector } from 'react-redux';

/**
 * Hook personnalis pour accder facilement  l'tat d'authentification.
 * @returns {{
 *  user: object | null,
 *  token: string | null,
 *  status: ('idle'|'loading'|'succeeded'|'failed'),
 *  isAuthenticated: boolean,
 *  isLoading: boolean,
 *  isSuccess: boolean,
 *  isError: boolean,
 *  message: string,
 *  permissions: Set<string>
 * }} Un objet contenant l'tat d'authentification complet.
 */
export const useAuth = () => {
  // Slectionne le slice 'auth' entier depuis le store Redux.
  const { user, token, status, isSuccess, isError, message } = useSelector(
    (state) => state.auth
  );

  // `useMemo` est utilis pour optimiser les re-rendus.
  // L'objet retourn ne changera de rfrence que si une de ses
  // dpendances (dans le tableau de dpendances) a rellement chang.
  return useMemo(
    () => ({
      user,
      token,
      status: status || 'idle', // Fournit 'idle' par dfaut
      isSuccess,
      isError,
      message,
      // Les permissions sont stockes dans l'objet user, on les expose ici
      // sous forme de Set pour des vrifications rapides (O(1)).
      permissions: new Set(user?.permissions || []),

      // --- Valeurs drives pour la simplicit d'utilisation ---
      isLoading: status === 'loading',
      // `isAuthenticated` est vrai uniquement si on a un user et un token.
      isAuthenticated: !!user && !!token,
    }),
    [user, token, status, isSuccess, isError, message]
  );
};