// ==============================================================================
//           Composant d'Aiguillage Racine (Root)
//
// MISE  JOUR : Utilise maintenant l'tat `status` de l'authentification pour
// une gestion fiable du chargement initial, sans tat local ni useEffect.
// ==============================================================================

import React from 'react';
import { Navigate } from 'react-router-dom';
import { useAuth } from '../hooks/useAuth';
import Loader from '../components/common/Loader';

const Root = () => {
  // 1. On rcupre l'tat d'authentification complet via notre hook.
  // `status` peut tre 'idle', 'loading', 'succeeded', ou 'failed'.
  const { isAuthenticated, status } = useAuth();

  // 2. Tant que le statut est indtermin ('idle') ou en cours de chargement ('loading'),
  // on affiche un loader. Cela empche toute redirection prmature avant
  // que l'tat d'authentification initial ne soit connu.
  if (status === 'idle' || status === 'loading') {
    return <Loader fullscreen text="Chargement de l'application..." />;
  }

  // 3. Une fois le statut connu, on redirige en toute scurit.
  // L'option `replace` est cruciale pour que l'utilisateur ne puisse pas
  // revenir  cette page d'aiguillage avec le bouton "prcdent".
  return <Navigate to={isAuthenticated ? '/dashboard' : '/login'} replace />;
};

export default Root;