// ==============================================================================
//                Composant Pied de Page (Footer)
//
// Ce composant affiche le pied de page de l'application.
// Il est conu pour tre simple et afficher des informations essentielles
// comme le copyright et la version de l'application.
//
// L'anne est calcule dynamiquement pour tre toujours  jour.
// ==============================================================================

import React from 'react';
import { Container } from 'react-bootstrap';

const Footer = () => {
  // Calcule l'anne actuelle pour le copyright.
  const currentYear = new Date().getFullYear();
  // Rcupre la version de l'application depuis les variables d'environnement Vite.
  // Fournit une valeur par dfaut si elle n'est pas dfinie.
  const appVersion = import.meta.env.VITE_APP_VERSION || '1.0.0';

  return (
    <footer className="footer border-top mt-auto py-3">
      <Container fluid>
        <div className="d-flex flex-column flex-sm-row justify-content-between align-items-center text-center">
          <small className="text-muted mb-2 mb-sm-0">
             {currentYear} {import.meta.env.VITE_APP_NAME || 'ERP Sngal'}. Tous droits rservs.
          </small>
          <small className="text-muted">
            Version {appVersion}
          </small>
        </div>
      </Container>
    </footer>
  );
};

export default Footer;