// ==============================================================================
//           Configuration Centrale du Client API avec Axios
//
// Ce module configure une instance unique d'Axios pour toutes les
// communications avec le backend.
//
// Il utilise des intercepteurs pour :
//   - Attacher automatiquement le token JWT  chaque requte.
//   - Grer globalement les erreurs d'authentification (401) en
//     dconnectant l'utilisateur.
//
// La fonction `injectStore` rsout le problme de dpendance circulaire
// entre ce module et le store Redux.
// ==============================================================================

import axios from 'axios';
import { logout } from '../store/slices/authSlice'; // L'action de dconnexion

// Une rfrence au store Redux qui sera injecte aprs sa cration.
let store;

/**
 * Fonction d'injection pour viter les dpendances circulaires.
 * Elle sera appele une seule fois depuis `client/src/store/index.js`.
 * @param {import('@reduxjs/toolkit').Store} _store - L'instance du store Redux.
 */
export const injectStore = (_store) => {
  store = _store;
};

// Cration de l'instance Axios de base
const apiClient = axios.create({
  // L'URL de base est dfinie dans les variables d'environnement Vite
  baseURL: import.meta.env.VITE_API_URL || 'http://localhost:5000/api/v1',
  headers: {
    'Content-Type': 'application/json',
  },
});


// --- Intercepteur de Requte ---
// S'excute AVANT chaque requte envoye.
apiClient.interceptors.request.use(
  (config) => {
    // Lire le token depuis l'tat Redux est plus fiable que le localStorage,
    // car il reprsente l'tat actuel de l'authentification.
    const token = store.getState().auth.token;
    if (token) {
      config.headers['Authorization'] = `Bearer ${token}`;
    }
    return config;
  },
  (error) => Promise.reject(error)
);


// --- Intercepteur de Rponse ---
// S'excute APRS chaque rponse reue (qu'elle soit russie ou en erreur).
apiClient.interceptors.response.use(
  (response) => response, // Si la rponse est russie, on la laisse passer.
  (error) => {
    // On vrifie si l'erreur est une erreur 401 (Non Autoris)
    const isAuthError = error.response && error.response.status === 401;
    const originalRequest = error.config;

    // viter une boucle infinie de dconnexion si la requte de login elle-mme choue
    if (isAuthError && !originalRequest.url.includes('/auth/login')) {
      console.error("Erreur 401 : Token invalide ou expir. Dconnexion de l'utilisateur...");
      
      // Utilise le store inject pour dispatcher l'action de dconnexion.
      // Cela va nettoyer l'tat de l'utilisateur et le rediriger vers la page de connexion.
      store.dispatch(logout());
    }

    // On propage l'erreur pour qu'elle puisse tre gre par le code qui
    // a initi l'appel (ex: dans un bloc try/catch ou un hook `useApi`).
    return Promise.reject(error);
  }
);

export default apiClient;